html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #fafafa;
  height: 100%;
  font-family: "Roboto", sans-serif;
}

.App {
  max-width: 768px;
  margin: auto;
}

.header-wrap {
  text-align: center;
}

.header-title {
  font-family: "Noto Sans TC", sans-serif;
  font-size: 18px;
  display: inline-block;
  background-color: #fafafa;
  position: relative;
  font-weight: 500;
  margin-top: 40px;
}

.header-title::after {
  content: "";
  width: 6px;
  height: 6px;
  background-color: #2096ba;
  position: absolute;
  border-radius: 50%;
  right: -10px;
  top: 6px;
}

.header-title::before {
  content: "";
  width: 6px;
  height: 6px;
  background-color: #2096ba;
  position: absolute;
  border-radius: 50%;
  right: -10px;
  bottom: 6px;
}

.question-card {
  background-color: #ffffff;
  -webkit-box-shadow: 0 3px 20px 0 rgba(32, 150, 186, 0.2);
          box-shadow: 0 3px 20px 0 rgba(32, 150, 186, 0.2);
  border-radius: 35px;
  margin: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 268px;
}

.question-card__subtitle {
  font-size: 16px;
  color: #5a5a5a;
  text-transform: capitalize;
}

.question-card__main-content {
  font-size: 50px;
  font-family: "Open Sans", sans-serif;
  margin: 0;
}

.question-card--animate {
  -webkit-animation-name: animate;
          animation-name: animate;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

.main-button {
  background: #2096ba;
  -webkit-box-shadow: 0 2px 6px 2px rgba(145, 145, 145, 0.4);
          box-shadow: 0 2px 6px 2px rgba(145, 145, 145, 0.4);
  border-radius: 32px;
  font-size: 20px;
  width: calc(100% - 60px);
  margin-left: 30px;
  border: 0;
  padding: 12px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-family: "Roboto", sans-serif;
  margin-top: 50px;
  color: #ffffff !important;
  text-decoration: none;
}

@-webkit-keyframes animate {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
  50% {
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes animate {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
  50% {
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

.answer-wrap {
  width: 100%;
  position: fixed;
  height: calc(100vh + 50px);
  top: 100%;
  background-color: #f0f0f0;
  -webkit-transition: all 0.15s ease-out;
  -o-transition: all 0.15s ease-out;
  transition: all 0.15s ease-out;
}

.answer-wrap--show {
  top: 0;
  z-index: 2;
}

.answer-main {
  margin-top: 180px;
  text-align: center;
}

.answer-main__secondary {
  font-size: 16px;
  color: #5a5a5a;
  text-transform: capitalize;
}

.answer-main__primary {
  font-size: 50px;
  font-family: "Noto Sans TC", sans-serif;
  font-weight: bolder;
  margin: 0;
}

.answer-button {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: #444444;
  text-decoration: none;
  text-transform: capitalize;
  height: 56px;
  background-color: #f0f0f0;
  position: absolute;
  bottom: 0;
  z-index: 3;
  -webkit-transition: all 0.1s ease-out;
  -o-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
}

.answer-button--activated {
  background-color: #2096ba;
  color: #ffffff;
}



